@import "../../global.scss";


.topbar{
    width: 100%;
    height: 70px;
    background-color: #23283e;
    color: #bdbddd;
    position: fixed;
    top: 0;
    z-index: 2;

    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left{
            display: flex;
            align-items: center;
            .logo{
                font-size: 30px;
                font-weight: 700;
                text-decoration: none;
                color: #bdbddd;
                margin-right: 40px;
            }
        }


       
    }


}