@import "../../global.scss";
.work{
    background-color: #23283e;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow{
        height: 50px;
        position: absolute;
        cursor: pointer;
        @include mobile{
            margin-top: 30em;
        }
        &.left{
            left: 100px;
            transform: rotate(90deg);
        }
        &.right{
            right: 100px;
            transform: rotate(-90deg);
        }
    }

    h1{
        color: #4880c4;
        font-size: 50px;
        margin-bottom: 500px;
    }

    .slider{
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease-out ;

        .container{
            width: 100vw;
            display: flex;
            justify-content: center;
            align-items: center;

            .item{
                width: 700px;
                height: 100%;
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                .left{
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftcontainer{
                        width: 90%;
                        height: 70%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                    }
                    
                    .imgContainer{
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        display: flex;
                        align-items: center;
                        justify-items: center;
                        background-color: rgb(238, 232, 199);


                    }
                }
                h2{
                    font-size: 20px;
                }
                p{
                    font-size: 13px;
                }
                span{
                    font-size: 12px;
                    font-weight:600;
                    text-decoration: underline;
                    cursor: pointer;
                }
                .right{
                    flex: 8;
                    height:100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    overflow: hidden;

                    img{
                        width: 700px;
                        transform: rotate(deg);
                    }
                }
            }
        }
    }


  

}